import React, { useState } from 'react';

const Sign = () => {

    const [email, setEmail] = useState("");
    const [password, setPassword] = useState("");

    const [allUser, newUser] = useState([]);

    const submitForm = (saveData) => {

        saveData.preventDefault();

        const newData = { email: email, password:password };

        newUser(...allUser, [newData]);
    }
    
    return (
        <>
            <div className="container-sm bg-light shadow-sm rounded p-6 form">
            <form method="post" onSubmit={submitForm}>
                <div className="form-floating mb-3">
                    <input type="email" className="form-control" name="email" id="email"
                    value={email}
                    onChange={(e) => setEmail(e.target.value)}
                    placeholder="name@example.com"/>
                    <label for="floatingInput">Email address</label>
                </div>
                <div className="form-floating mb-3">
                    <input type="password" className="form-control" name="password" id="password"
                    value={password}
                    onChange={(p) => setPassword(p.target.value)}
                    placeholder="Password"/>
                    <label for="floatingPassword">Password</label>
                </div>
                <div className="d-grid">
                <button type="submit" class="btn btn-success btn-lg">Sign in</button>
                </div>
            </form>
            </div>
        </>
    )
}

export default Sign;