import React, { useState } from 'react';

const Contact = () => {

    const [data, setData] = useState({
    fullName: "",
    phone: "",
    email: "",
    suggestions: "",
    });

    const InputEvent = (event) => {
        const { name, value } = event.target;

        setData((preval) => {
            return {
                ...preval,
                [name]: value,
            };
        });
    };

    const formSubmit = (e) => {
        e.preventDefault();
        alert(`Name: ${data.fullName}
        Number: ${data.phone}
        Email: ${data.email}
        Suggestion: ${data.suggestions}` )
    };

    return (
        <>
            <div className="my-5">
                <h1 className="text-center">Contact us</h1>
            </div>
            <div className="container contact_div">
                <div className="row">
                    <div className="col-md-6 col-10 mx-auto">
                        <form onSubmit={formSubmit}>
                        <div className="mb-3">
                            <label for="exampleFormControlInput1" className="form-label">Full Name</label>
                            <input type="text" className="form-control" id="exampleFormControlInput1" name="fullName" value={data.fullName} onChange={InputEvent} placeholder="Enter your name"/>
                        </div>
                        <div className="mb-3">
                            <label for="exampleFormControlInput1" className="form-label">Phone</label>
                            <input type="number" className="form-control" id="exampleFormControlInput1" name="phone" value={data.phone} onChange={InputEvent} placeholder="Phone number"/>
                        </div>
                        <div className="mb-3">
                            <label for="exampleFormControlInput1" className="form-label">Email address</label>
                            <input type="email" className="form-control" id="exampleFormControlInput1" name="email" value={data.email} onChange={InputEvent} placeholder="name@example.com"/>
                        </div>
                        <div className="mb-3">
                            <label for="exampleFormControlTextarea1" className="form-label">Suggestions</label>
                            <textarea className="form-control" id="exampleFormControlTextarea1" rows="3" name="suggestions" value={data.suggestions} onChange={InputEvent}></textarea>
                        </div>
                        <div className="d-grid">
                            <button className="btn btn-primary" type="submit">Submit form</button>
                        </div>
                    </form>
                    </div>
                </div>
            </div>
        </>
    )
}

export default Contact;